body {
  padding: 0;
  margin: 0;

  //        iOS
  text-size-adjust: 100%;

  //       iOS
  -webkit-tap-highlight-color: transparent;

  //     Mac
  -webkit-font-smoothing: antialiased;
}

//     Mac  Firefox
@-moz-document url-prefix("") {
  body {
    font-weight: 200;
    -moz-osx-font-smoothing: grayscale;
  }
}

// IE fix
main {
  display: block;
}

button,
input,
textarea,
select {
  font: inherit;
}
